import React from 'react'
import { FaTimes } from 'react-icons/fa'
import sublinks from './data'
import { useOpenModal } from './context'

const Sidebar = () => {
  const { isModalOpen, closeModal } = useOpenModal()

  return (
    <div
      className={`${isModalOpen ? 'sidebar-wrapper show' : 'sidebar-wrapper'}`}
    >
      <aside className="sidebar-center">
        <button className="sidebar_close" onClick={closeModal}>
          <FaTimes />
        </button>
        <div className="sidebar_content">
          {sublinks.map((link, index) => {
            const { page, links } = link
            return (
              <article key={index}>
                <h4>{page}</h4>
                <div className="sublinks">
                  {links.map((item, index) => {
                    const { label, icon, url } = item
                    return (
                      <a key={index} href={url}>
                        {icon}
                        {label}
                      </a>
                    )
                  })}
                </div>
              </article>
            )
          })}
        </div>
      </aside>
    </div>
  )
}

export default Sidebar
