import React, { useState, useContext } from 'react'
import sublinks from './data'

const ModalContext = React.createContext()
const SubMenuContext = React.createContext()

const AppProvider = ({ children }) => {
  const [isModalOpen, setIsModalOpent] = useState(false)
  const [isSubMenuOpen, setIsSubMenuOpen] = useState(false)
  const [location, setLocation] = useState({})
  const [page, setPage] = useState({ page: '', links: [] })
  const openModal = () => {
    setIsModalOpent(true)
  }
  const closeModal = () => {
    setIsModalOpent(false)
  }

  const openSubMenu = (text, coordinates) => {
    const newPage = sublinks.find((link) => link.page === text)
    setPage(newPage)

    setLocation(coordinates)
    setIsSubMenuOpen(true)
  }

  const closeSubMenu = () => {
    console.log('close')
    setIsSubMenuOpen(false)
  }

  return (
    <ModalContext.Provider value={{ isModalOpen, openModal, closeModal }}>
      <SubMenuContext.Provider
        value={{ isSubMenuOpen, openSubMenu, closeSubMenu, location, page }}
      >
        {children}
      </SubMenuContext.Provider>
    </ModalContext.Provider>
  )
}
export const useOpenModal = () => {
  return useContext(ModalContext)
}

export const useOpenSubMenu = () => {
  return useContext(SubMenuContext)
}
export { AppProvider }
