import React from 'react'
import logo from './images/logo.svg'
import { FaBars } from 'react-icons/fa'

import { useOpenModal, useOpenSubMenu } from './context'

const Navbar = () => {
  const { openModal } = useOpenModal()
  const { openSubMenu, closeSubMenu } = useOpenSubMenu()

  const displaySubmenu = (e) => {
    const element = e.target.textContent
    const temp = e.target.getBoundingClientRect()
    const center = (temp.left + temp.right) / 2
    const bottom = temp.bottom + 10
    openSubMenu(element, { center, bottom })
  }
  const handleSubmenu = (e) => {
    if (!e.target.classList.contains('nav-link')) {
      closeSubMenu()
    }
  }
  return (
    <nav className="navigation" onMouseOver={handleSubmenu}>
      <div className="nav_center">
        <div className="nav_header">
          <img src={logo} alt="logo" />
          <button className="btn btn_icon" onClick={openModal}>
            <FaBars />
          </button>
        </div>

        <ul className="nan_list">
          <li>
            <button className="nav-link" onMouseOver={displaySubmenu}>
              products
            </button>
          </li>
          <li>
            <button className="nav-link" onMouseOver={displaySubmenu}>
              developers
            </button>
          </li>
          <li>
            <button className="nav-link" onMouseOver={displaySubmenu}>
              company
            </button>
          </li>
        </ul>

        <button className="btn sing_btn">Sing in</button>
      </div>
    </nav>
  )
}

export default Navbar
